import Vue from 'vue';
import VueRouter from 'vue-router';

// components
import Home from '@/views/home/Home.vue';
import Fallback from '@/views/Fallback.vue';

Vue.use(VueRouter);

const routes = [
	{
		path: '',
		name: 'home',
		components: {
			default: Home,
			Header: () => import('@/components/home/HomeHeader.vue')
		}
	},
	{
		path: '/blog',
		name: 'Blog',
		components: {
			default: () => import(/* webpackChunkName: "blog" */ '@/views/blog/Blog.vue'),
			Header: () => import('@/components/blog/BlogHeader.vue')
		}
	},
	{
		path: '*',
		name: 'Fallback',
		component: Fallback
	}
];

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
});

export default router;
